body{
    font-family: "Neue Haas Unica";
    background-color: black;
}
html, body, #root, .container{
    display: flex;
    justify-content: center;
    height: 100%;
}
h3, h5, p{
    margin-top: 0;
    margin-bottom: 0;
}
.loader{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    
}
.movies{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-auto-rows: auto;
    justify-items: center;
    align-items: center;
    width: 85%;
    margin-top: 10vh;   
}
.movies .movie{
    width: 93%;
    height: 55vh;
    background-color: white;
    margin-bottom: 10vh;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
}
.movie__data{
    margin: 2vh 2vh;
    font-size: 1rem;
}
img {
    margin: 2vh 2vh;
    width: 30%;
}